.modal-content {
  background: #202124;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  color: white;
  font-size: 14px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  position: relative;
}

/* Close Button */
.close-button {
